<template>
    <v-data-table
        :headers="headers"
        :items="tabledata"
        :options.sync="options"
        :expanded.sync="expanded"
        fixed-header
        show-expand
        :height="height"
        item-key="_id"
        no-data-text="暂无数据"
        :server-items-length="total_rows"
        :footer-props="{
            'items-per-page-options': items_per_page_options,
            'show-first-last-page':true,
        }"
        class="elevation-4"
        >
        <template v-slot:top>
        <v-toolbar flat>
          <v-toolbar-title>数据表</v-toolbar-title>
          <v-spacer></v-spacer>
        <v-btn    color="primary" fab   small @click="update_col_page_data">
        <v-icon style="height:auto">refresh</v-icon>                  
        </v-btn>
        </v-toolbar>
      </template>
      <template v-slot:expanded-item="{ headers, item }">
        <td :colspan="headers.length"><pre>{{item}}</pre></td>
      </template>

    </v-data-table>
</template>

<script>
// 扩展表格显示
// 2020/5/25 by oldkingnearby@gmail.com
import { sync,get } from 'vuex-pathify'
export default {
    data:()=>({
        height:400,
        expanded: [],
        singleExpand:true,//只允许单行扩展
        // 分页数据管理
        options:{},
    }),
    computed: {
        
        items_per_page_options:sync('db/items_per_page_options'),
        // 显示的数据库配置
        collection_config:sync('db/collection_config'),
        // 表头
        headers:get('db/headers'),
        // 表内数据
        tabledata:get('db/tabledata'),
        // 总行数
        total_rows:get('db/total_rows'),
    },
    methods:{
        // 刷新表格内容
        update_col_page_data(){
            this.$store.dispatch("db/update_col_page_data",this.options);
        },
    },
    watch:{
        options:{
            handler(){
                console.log("页面刷新",this.options)
                this.$store.dispatch("db/update_col_page_data",this.options);
            },
            deep:true,
        },
    }

}
</script>